import '../../frame/frame.js';
import '../center-common.js';
import './system-center.less';

var app = angular.module('systemCenterApp', ['gm.ngCustom.control', 'ngResource']);
app.controller('bannerCtrl', ['$scope', '$gmRestful', '$location', function ($scope, $gmRestful, $location) {
    $scope.backgoundImg = GLOBAL_INDEX_CONFIG.backgoundImg;
    $scope.logImg = GLOBAL_INDEX_CONFIG.logImg;
    $scope.systemName = GLOBAL_INDEX_CONFIG.systemName;
    $scope.EnglishSystemName = GLOBAL_INDEX_CONFIG.EnglishSystemName;

    //数据准备
    var pRestful = new $gmRestful();
    GM.FrameManager.prepareData($scope, pRestful);

    //暂时用的假头像和用户名
    if ($scope.user) {
        $scope.user.avatar = {
            background: 'url(' + require('./user.png') + ') no-repeat 0 0 / contain',
            'border-radius': '50px'
        };
    }

    //获取登出路径
    var strPath = GM.LayoutOper.getURLWithoutParams($location.absUrl());
    $scope.frameData = {
        loginUrl: LOGIN_PATH + '?service=' + strPath + '&logout=1'
    };

    //登出
    $scope.logout = function ($event) {
        GM.FrameManager.logout();
        $event.preventDefault();
    };
}]);
app.controller('swiperBoxCtrl', ['$scope', '$timeout', function ($scope, $timeout) {
    //获取APP子系统列表信息
    var arrayApps = [];
    for (var ii = 0; ii < GM.AppInfos.length; ii++) {
        var appItem = GM.AppInfos[ii];
        var bHasPermission = GM.FrameManager.hasPermission(appItem.name);
        if (bHasPermission) {
            var addItem = {
                img: appItem.icon,
                url: appItem.host,
                txt: appItem.title,
                title: appItem.title,
                permission: appItem.name
            };
            arrayApps.push(addItem);
        }
    }

    $scope.swiperList = arrayApps;
    $timeout(function () {
        var mySwiper;
        if ($scope.swiperList.length < 7) {
            mySwiper = new Swiper('.swiper-container', {
                width: (window.screen.width / 6),
                nextButton: '.swiper-button-next',
                prevButton: '.swiper-button-prev'
            });
        } else {
            mySwiper = new Swiper('.swiper-container', {
                width: (window.screen.width / 6),
                slidesOffsetBefore: 50,
                slidesOffsetAfter: 50,
                nextButton: '.swiper-button-next',
                prevButton: '.swiper-button-prev'
            });
        }
        mySwiper.onResize();
    }, 10);
    $scope.goSubsystem = function (strUrl) {
        GM.CenterCommon.openSystem(strUrl);
        return false;
    };
}]);
app.controller('footerCtrl', ['$scope', function ($scope) {
    $scope.Copyright = GLOBAL_INDEX_CONFIG.Copyright;
}]);
app.run(['$templateCache', function ($templateCache) {
    let strTem = require('./system-center.html');
    $templateCache.put('system-center.tpl.html', strTem);
}]);

//验证token
GM.FrameManager.addToken(app);



